.news {

  &__top {

    @include onTablet {
      display: flex;
      justify-content: space-between;
      max-width: 768px;
      margin: 0 auto;
    }
  }

  &__item--first {

    @include onTablet {
      grid-column: 1 / 4;
    }
  }

  &__item--second {
    @include onTablet {
      grid-column: 4 / 7;
    }
  }

  &__title {
    margin: 0 0 60px;
    text-align: center;
    font-weight: 700;
    font-size: 28px;
    line-height: 35px;

    color: #1C1B29;

    @include onTablet {
      margin: 0 0 75px;
      text-align: left;
      font-weight: 700;
      font-size: 40px;
      line-height: 50px;
    }
  }

  &__photo-container {
    margin: 0 auto 20px;

    @include onTablet {
      margin: 0 auto 30px;
      min-width: 330px;
      min-height: 330px;
    }
  }

  &__image {
    display: block;
    margin: 0 auto;
    width: 100%;
    height: 100%;
  }

  &__date {
    display: block;
    margin: 0 0 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;

    color: #CD4D31;
  }

  &__subtitle {
    margin: 0 0 20px;
    font-weight: 700;
    font-size: 20px;
    line-height: 25px;

    color: #1C1B29;

    @include onTablet {
      font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    }

  }

  &__description {
    display: block;
    margin: 0 0 30px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;

    color: #504F5E;

    @include onTablet{
      margin: 0 0 40px;

    }
  }

}

