.coming-soon {
  &__item {
    display: block;
    // max-width: 280px;
    margin: 0 auto;
  }

  &__item {

    @include onTablet {
      display: grid;
      margin: 0 auto 70px;
      grid-template-columns: repeat(6,1fr);
      column-gap: 30px;
      min-width: 768px;
    }
  }

  &__top {

    @include onTablet {
      display: flex;
      justify-content: space-between;
      max-width: 768px;
      margin: 0 auto;
    }
  }


  &__bottom {
    @include onTablet {
      max-width: 330px;
      grid-column: 4 / 7;
    }
  }


  &__events {
    margin: 0 0 60px;
    text-align: center;
    font-weight: 700;
    font-size: 28px;
    line-height: 35px;

    color: #1C1B29;

    @include onTablet {
      // grid-column: 1 / 4;
      font-weight: 700;
      font-size: 40px;
      line-height: 50px;
    }
  }

  &__photo-container {
    margin: 0 auto 20px;

    @include onTablet {
      margin: 0;
      grid-column: 1 / 4;;
    }
  }

  &__photo {
    display: block;
    margin: 0 auto;
    max-width: 280px;
    max-height: 356px;

    @include onTablet {
      margin: 0;
      min-width: 100%;
      min-height: 422px;
    }
  }

  &__date {
    display: block;
    margin: 0 0 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;

    color: #CD4D31;
  }

  &__subtitle {
    margin: 0 0 20px;
    font-weight: 700;
    font-size: 20px;
    line-height: 25px;

    color: #1C1B29;

    @include onTablet {
      font-size: 24px;
      line-height: 30px;
    }
  }

  &__description {
    display: block;
    margin: 0 0 30px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;

    color: #504F5E;
  }

}
